// ** React Imports
import { useState } from "react"
import { Link, useHistory } from 'react-router-dom'
import { connect, useSelector } from 'react-redux'

// ** Custom Components
import UserViewAvatar from '@components/user-avatar/UserViewAvatar'
import Avatar from '@components/avatar'

// ** Third Party Components
import { Card, CardBody, CardText, Button, Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Media } from 'reactstrap'
import { DollarSign, TrendingUp, User, Check, Star, Flag, Phone, Calendar, Feather } from 'react-feather'

// ** Store Actions
import actions from '@store/storeConfig/actions'

import { DATE_FORMATS, formatDate, formatPhone } from '@helper'

const VendorInfoCard = ({ selectedVendor, deactivateVendor, deleteVendor }) => {
  const history = useHistory()
  const [disabledModal, setDisabledModal] = useState(false)
  const [modalTitle, setModalTitle] = useState('')
  const [modalDesc, setModalDesc] = useState('')
  const [action, setAction] = useState('')

  const { rolesConfig } = useSelector(state => state.roles)
  const { entityType, status } = rolesConfig.vendor

  const handleConfirm = () => {
    setDisabledModal(false)
    if (action === 'deactivate') {
      deactivateVendor(selectedVendor._id)
    } else if (action === 'delete') {
      deleteVendor(selectedVendor._id)
      history.push("/vendors/list")
    }
  }

  return (
    <>
      <Card>
        <CardBody>
          <Row>
            <Col xl='4' lg='12' className='d-flex flex-column justify-content-between border-container-lg'>
              <UserViewAvatar
                name={selectedVendor.vendorName}
                email={selectedVendor.email}
                photo={selectedVendor.profilePictureURL}
              />
              <div className='d-flex align-items-center user-total-numbers'>
                <div className='d-flex align-items-center mr-2'>
                  <div className='color-box bg-light-primary'>
                    <DollarSign className='text-primary' />
                  </div>
                  <div className='ml-1'>
                    <h5 className='mb-0'>23,900</h5>
                    <small>Total Spend</small>
                  </div>
                </div>
                <div className='d-flex align-items-center'>
                  <div className='color-box bg-light-success'>
                    <TrendingUp className='text-success' />
                  </div>
                  <div className='ml-1'>
                    <h5 className='mb-0'>13</h5>
                    <small>Average Orders</small>
                  </div>
                </div>
              </div>
            </Col>
            <Col xl='3' lg='12' className='mt-2 mt-xl-0'>
              <div className='user-info-wrapper'>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<User size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Owner Name</h6>
                    <small>{selectedVendor.ownerName}</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Phone size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Phone Number</h6>
                    <small>{formatPhone(`${selectedVendor.countryCode} ${selectedVendor.phoneNumber}`) }</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Check size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Account Status</h6>
                    <small>{status[selectedVendor.status] !== undefined  ? status[selectedVendor.status] : ''}</small>
                  </Media>
                </Media>
              </div>
            </Col>
            <Col xl='3' lg='12' className='mt-2 mt-xl-0'>
              <div className='user-info-wrapper'>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Calendar size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Account Creation Date</h6>
                    <small>{formatDate(selectedVendor.createdAt, DATE_FORMATS.YMD_HMS)}</small>
                  </Media>
                </Media>
                <Media className='mt-2'>
                  <Avatar color='light-primary' className='rounded mr-1' icon={<Feather size={18} />} />
                  <Media body>
                    <h6 className='mb-0'>Entity Type</h6>
                    <small>{entityType[selectedVendor.entityType] !== undefined ? entityType[selectedVendor.entityType]  : 'Not Saved'}</small>
                  </Media>
                </Media>
              </div>
            </Col>
            <Col xl='2' lg='12' className='mt-2 mt-xl-0'>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  color='primary'
                  tag={Link} to={`/vendors/edit/${selectedVendor._id}`}>
                  Edit
                </Button.Ripple>
              </div>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  outline
                  onClick={() => {
                    setAction('deactivate')
                    setModalTitle('Deactivate User')
                    setModalDesc('Are you sure you want to deactivate the user')
                    setDisabledModal(true)
                  }}
                  color='danger'>
                  Deactivate
                </Button.Ripple>
              </div>
              <div className='d-flex flex-wrap align-items-center my-50'>
                <Button.Ripple
                  block
                  color='danger'
                  onClick={() => {
                    setAction('delete')
                    setModalTitle('Delete User')
                    setModalDesc('Are you sure you want to delete the user')
                    setDisabledModal(true)
                  }}
                  outline>
                  Delete
                </Button.Ripple>
              </div>
            </Col>
          </Row>
        </CardBody>
      </Card>
      <Modal
        isOpen={disabledModal}
        toggle={() => setDisabledModal(!disabledModal)}
      >
        <ModalHeader toggle={() => setDisabledModal(!disabledModal)}>{modalTitle}</ModalHeader>
        <ModalBody>
          {modalDesc}
        </ModalBody>
        <ModalFooter>
          <Button color='primary' onClick={handleConfirm}>
            Yes
          </Button>
          <Button color='primary' onClick={() => setDisabledModal(!disabledModal)} outline>
            No
          </Button>
        </ModalFooter>
      </Modal>
    </>
  )
}

const mapStateToProps = state => {
  return {
  }
}

export default connect(mapStateToProps, actions)(VendorInfoCard)
