// ** React Import
import { useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'

// ** Custom Components
import Sidebar from '@components/sidebar'
import InputGroup from '@components/input-group'
import UILoader from '@components/ui-loader'

// ** Utils

// ** Third Party Components
import classnames from 'classnames'
import { Button, Form, FormGroup } from 'reactstrap'

// ** Store & Actions
import { addVendorEmployee, vendorEmployeeFormChange } from '@store/Vendors/actions'

const SidebarNewVendorEmployees = ({
  open,
  toggleSidebar
}) => {
  const formEl = useRef(null)

  // ** store and dispatch
  const { vendorEmployeeFormData: employeeFormData, employeeFormErrors: formErrors, isLoading, isSubmitting } = useSelector(state => state.vendors),
    dispatch = useDispatch()
  const { rolesConfig } = useSelector(state => state.roles)
  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(addVendorEmployee())
  }

  const handleFormChange = (name, value) => {
    dispatch(vendorEmployeeFormChange(name, value))
  }

  const { roles, status } = rolesConfig.vendorEmployee
  const roleOptions = Object.keys(roles).map((rKey) => {
    return (
      <option key={rKey} value={rKey}>{roles[rKey]}</option>
    )
  })

  return (
    <Sidebar
      size='lg'
      open={open}
      title='New Employee'
      headerClassName='mb-1'
      contentClassName='pt-0'
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit} innerRef={formEl}>
        <InputGroup
          type={'text'}
          label={'First Name'}
          error={formErrors['firstName']}
          name={'firstName'}
          placeholder={'First Name'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={employeeFormData.firstName}
          required={true}
          className={classnames({ 'is-invalid': formErrors['firstName'] })}
        />
        <InputGroup
          type={'text'}
          label={'Last Name'}
          error={formErrors['lastName']}
          name={'lastName'}
          placeholder={'Last Name'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={employeeFormData.lastName}
          required={true}
          className={classnames({ 'is-invalid': formErrors['lastName'] })}
        />
        <InputGroup
          type={'text'}
          label={'Username'}
          error={formErrors['userName']}
          name={'userName'}
          placeholder={'Username'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={employeeFormData.userName}
          required={true}
          className={classnames({ 'is-invalid': formErrors['userName'] })}
        />
        <InputGroup
          type={'email'}
          label={'Email'}
          error={formErrors['email']}
          name={'email'}
          placeholder={'Email'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={employeeFormData.email}
          required={true}
          className={classnames({ 'is-invalid': formErrors['email'] })}
        />
        <InputGroup
          type={'select'}
          label={'Country Code'}
          error={formErrors['countryCode']}
          name={'countryCode'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={employeeFormData.countryCode}
          required={true}
          className={classnames({ 'is-invalid': formErrors['countryCode'] })}
        >
          <option value=''>Select Country Code</option>
          <option value='254'>+254 Kenya</option>
        </InputGroup>
        <InputGroup
          type={'number'}
          label={'Contact'}
          error={formErrors['phoneNumber']}
          name={'phoneNumber'}
          placeholder={'Contact'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={employeeFormData.phoneNumber}
          required={true}
          className={classnames({ 'is-invalid': formErrors['phoneNumber'] })}
        />
        <InputGroup
          type={'password'}
          label={'Password'}
          error={formErrors['password']}
          name={'password'}
          placeholder={'Password'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={employeeFormData.password}
          required={true}
          className={classnames({ 'is-invalid': formErrors['password'] })}
        />
        <InputGroup
          type={'select'}
          label={'Employee Role'}
          error={formErrors['role']}
          name={'role'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={employeeFormData.role}
          className={classnames({ 'is-invalid': formErrors['role'] })}
        >
          <option value=''>Select Role</option>
          {roleOptions}
        </InputGroup>
        <FormGroup row className='mr-0 ml-0'>
          <UILoader blocking={isLoading} className='mr-1'>
            <Button type='submit' color='primary'>
              Submit
            </Button>
          </UILoader>
          <Button type='reset' color='secondary' outline onClick={toggleSidebar}>
            Cancel
          </Button>
        </FormGroup>
      </Form>
    </Sidebar>
  )
}

export default SidebarNewVendorEmployees
