// ** React Imports
import { useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** Store & Actions
import { getVendor } from '@store/Vendors/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Reactstrap
import { Alert, Col, Row } from 'reactstrap'

// ** Custom Components
import UILoader from '@components/ui-loader'

// ** Vendor View Components
import VendorInfoCard from './VendorInfoCard'
// import VendorEmployees from './VendorEmployees'
import VendorEmployees from '../../VendorEmployee/List/VendorEmployee'

// ** Styles
import '@styles/react/apps/app-users.scss'

const VendorView = () => {
  // ** Vars
  const store = useSelector(state => state.vendors),
    dispatch = useDispatch(),
    { id } = useParams()

  const { isLoading } = store

  // ** Get suer on mount
  useEffect(() => {
    dispatch(getVendor(id))
  }, [dispatch])

  return store.selectedVendor !== null && store.selectedVendor !== undefined ? (
    <div className='app-user-view'>
      <Row>
        <Col xl='12' lg='12' md='12'>
          <VendorInfoCard selectedVendor={store.selectedVendor} />
        </Col>
      </Row>
      <Row>
        <Col xl='12' lg='12' md='12'>
          <UILoader blocking={isLoading}>
            <div className='app-user-list'>
              <VendorEmployees />
            </div>
          </UILoader>
        </Col>
      </Row>
    </div>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>Vendor not found</h4>
      <div className='alert-body'>
        Vendor with id: {id} doesn't exist. Check list of all Vendors: <Link to='/vendors/list'>Vendors List</Link>
      </div>
    </Alert>
  )
}

export default VendorView
