import { useState, useEffect } from "react"
import { useDispatch, useSelector } from 'react-redux'

// ** Invoice List Sidebar
import Sidebar from './EmployeeSidebar'

// ** Columns
import { EmployeeColumns } from './EmployeeColumns'

// ** Store & Actions
import { getAllVendorEmployees, getVendorEmployeeList } from '@store/Vendors/actions'

// ** Custom Components
import CarhootTable from '@components/table/CarhootTable'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import '@styles/react/apps/app-users.scss'

const VendorEmployeeTab = () => {
// ** States
  const [currentRole, setCurrentRole] = useState({ value: '', label: 'Select Role' })
  const [currentStatus, setCurrentStatus] = useState({ value: '', label: 'Select Status' })
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const { rolesConfig } = useSelector(state => state.roles)

  const { employeeAllData, employeeData, employeeTotal, selectedVendor } = useSelector(state => state.vendors)
  const { roles, status } = rolesConfig.vendorEmployee
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(getAllVendorEmployees(selectedVendor._id))
    dispatch(
      getVendorEmployeeList({
        vendorId: selectedVendor._id
      })
    )
  }, [selectedVendor])


  const roleOptions = [{ value: '', label: 'Select Role' }]
  for (const rKey in roles) {
    roleOptions.push({ value: rKey, label: roles[rKey] })
  }
  const statusOptions = [{ value: '', label: 'Select Status' }]
  for (const sKey in status) {
    statusOptions.push({ value: sKey, label: status[sKey] })
  }

  const customFilters = [
    {
      key: 'role',
      options: roleOptions,
      value: currentRole,
      onChange: setCurrentRole
    },
    {
      key: 'status',
      options: statusOptions,
      value: currentStatus,
      onChange: setCurrentStatus
    }
  ]

  const searchParams = {
    vendorId: selectedVendor._id,
    role: currentRole.value,
    status: currentStatus.value
  }

  // ** Function to toggle sidebar
  const toggleSidebar = () => setSidebarOpen(!sidebarOpen)

  const getAllEmployees = () => {
    return (dispatch) => {
      dispatch(getAllVendorEmployees(selectedVendor._id))
    }
  }

  return (
    <div className='app-user-list'>
      <CarhootTable
        allData={employeeAllData}
        data={employeeData}
        total={employeeTotal}
        columns={EmployeeColumns}
        filters={customFilters}
        searchParams={searchParams}
        getList={getVendorEmployeeList}
        getAllList={getAllEmployees}
        headerActionHandler={toggleSidebar}
        headerActionLabel={'Add New Employee'}
        customHeaderLabel={'Search Vendor Employees'}
        tableLabel={'Vendor Employees'}
      />
      <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} />
    </div>
  )
}

export default VendorEmployeeTab
